import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import superagent from 'superagent'

export const createProposal = createAsyncThunk('governance/createProposal', async payload => {
  let resp = await superagent.put("http://localhost:8090/governance/createProposal")
    .send(payload)
  
  console.log("createProposal resp", resp.body)

  return resp.body
})

export const vote = createAsyncThunk('governance/vote', async payload => {
  let resp = await superagent.put("http://localhost:8090/governance/vote")
    .send(payload)

  console.log("vote resp", resp.body)

  return payload
})

export const execute = createAsyncThunk("governance/execute", async (payload) => {
  let resp = await superagent
    .put("http://localhost:8090/governance/execute")
    .send(payload);

  console.log("execute resp", resp.body);

  return payload;
});



export const selectProposals = state => {
  return state.governance.proposals
}

export const selectCurrentStake = state => {
  return state.governance.currentStake
}

export const selectPoolSize = state => {
  return state.governance.poolSize
}

