import { useSelector } from 'react-redux'
import { selectTotalSupplyInUsd, selectTotalBorrowInUsd, selectNetAPY } from '../features/wallet/walletSlice'

const NetAPY = props => {
  return (
    <div className="col-5 col-sm-4 text-center net-apy">
      <h4>Net APY</h4>
      <span className="net-apy-balance" aria-label="net-apy">
        {props.value.toFixed(2)}
      </span>
    </div>
  )
}

const Balance = props => {
  return (
    <>
      <h4>{props.title}</h4>
      <span className="balance" aria-label={props.ariaLabel}>
        $ {props.amount.toFixed(6)}
      </span>
    </>
  )
}

const Balances = () => {
  const totalSupply = useSelector(selectTotalSupplyInUsd)
  const totalBorrow = useSelector(selectTotalBorrowInUsd)
  const netAPY = useSelector(selectNetAPY)

  return (
    <div className="container balances">
      <div className="row">
        <div className="col-md-1 d-none d-sm-block" />
        <div className="col-md-3 d-none d-sm-block text-center supply">
          <div className="spacer-35" />
          <div className="spacer-35" />
          <div className="spacer-10" />
          <Balance title="Supply Balance" amount={totalSupply} ariaLabel={"total-supply-balance"} />
        </div>
        <NetAPY value={netAPY} />
        <div className="col-md-3 d-none d-sm-block text-center borrow">
          <div className="spacer-35" />
          <div className="spacer-35" />
          <div className="spacer-10" />
          <Balance title="Borrow Balance" amount={totalBorrow} ariaLabel={"total-borrow-balance"} />
        </div>
        <div className="col-md-1 d-none d-sm-block" />
        <div className="col-7 d-block d-sm-none">
          <div className="row">
            <div className="col text-center supply">
              <div className="spacer-20" />
              <Balance title="Supply Balance" amount={totalSupply} />
            </div>
          </div>
          <div className="row">
            <div className="col text-center borrow">
              <div className="spacer-35" />
              <Balance title="Borrow Balance" amount={totalBorrow} />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Balances