import React from "react"
import Site from './layout/Site'
import Governance from './layout/Governance'
import NewProposal from "./layout/NewProposal"
import Whitelist from "./layout/Whitelist"
import ProposalDetails from "./layout/ProposalDetails"
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom"
import Staking from "./layout/Staking"
import { useSelector } from "react-redux"


const App = () => {
  const theme = useSelector(state => state.theme.theme)
  return (
    <div id="site-container" className={theme === "light" ? "" : " dark-theme"} >
      <Router basename="/lq-app-demo" >
        <Switch>
          <Route exact path="/">
            <Site />
          </Route>
          <Route path="/vote">
            <Governance />
          </Route>
          <Route path="/staking">
            <Staking />
          </Route>
          <Route path="/new-proposal">
            <NewProposal />
          </Route>
          <Route path="/proposal-details/:proposalId">
            <ProposalDetails />
          </Route>
          <Route path="/whitelist">
            <Whitelist />
          </Route>
        </Switch>

        

        <div id="modal-content" />
      </Router>
    </div>
  )
}

export default App;