import { useSelector } from 'react-redux'
import { selectTotalBorrowLimitRatio, selectTotalBorrowLimitUSD } from '../features/wallet/walletSlice'

const Progress = () => {
  const borrowLimitRatio = useSelector(selectTotalBorrowLimitRatio)
  const totalBorrowLimitUSD = useSelector(selectTotalBorrowLimitUSD)
  return (
    <div className="container progress-area">
      <div className="row">
        <div className="col-5 col-md-3 col-lg-2 text-start text-sm-end">
          Borrow limit <strong>{borrowLimitRatio * 100}%</strong>
        </div>
        <div className="col-md-8 col-5">
          <div className="spacer-5" />
          <div className="progress" style={{ height: '10px' }}>
            <div className="progress-bar" role="progressbar" style={{ width: (borrowLimitRatio * 100) + '%' }} aria-valuenow={borrowLimitRatio * 100} aria-valuemin={0} aria-valuemax={100} />
          </div>
        </div>
        <div className="col-2 col-md-1 col-lg-2">
          <strong>${totalBorrowLimitUSD.toFixed(2)}</strong>
        </div>
      </div>
    </div>
  )
}

export default Progress