import React from 'react'
import { useSelector, shallowEqual } from 'react-redux'
import BorrowMarket from './BorrowMarket'
import { selectMarketIds } from '../market/marketSlice'


const Borrow = () => {
  const marketIds = useSelector(selectMarketIds, shallowEqual)

  return (
    <div className="marketers-box borrow-market align-self-end">
      <h3>Borrow Markets</h3>
      <div className="row headers">
        <div className="col-md-4 col-4">Asset</div>
        <div className="col-md-8 col-8">
          <div className="row">
            <div className="col-md-4 d-none d-sm-block text-end">APY</div>
            <div className="col-md-4 col-6 text-end">Wallet</div>
            <div className="col-md-4 col-6 text-end">Liquidity</div>
          </div>
        </div>
      </div>
      {marketIds.map(id => <BorrowMarket key={id} marketId={id} />)}
    </div>
  )
}

export default Borrow