import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";
import {
  selectPoolSize,
  selectProposalById,
  execute,
} from "../features/governance/governanceSlice";
import VoteModal from "../features/governance/VoteModal"
import VoteDetails from "../features/governance/VoteDetails"
import { unwrapResult } from "@reduxjs/toolkit";

export default function ProposalDetails() {
  // STATES: Draft, Voting, Expired/Rejected, Succeeded, Executed
  let { proposalId } = useParams()
  let proposal = useSelector(state => selectProposalById(state, parseInt(proposalId)))
  let [showVoteModal, setShowVoteModal] = useState(false)
  const poolSize = useSelector(selectPoolSize)
  const dispatch = useDispatch()
  const onExecute = async () => {
    unwrapResult(await dispatch(
      execute(proposal)
    ))
  }
  const executable = 
    proposal.votes.length > 0 
    && proposal.votes.map(v => v.balance).reduce((a,v) => a+v,0) >= (poolSize * 0.8)
  
  return (
    <div>
      {showVoteModal && (
        <VoteModal
          proposalId={proposalId}
          onClose={() => setShowVoteModal(false)}
        />
      )}
      <div className="spacer-20" />
      <div className="spacer-30" />
      <div className="container wider proposal-details">
        <div className="row">
          <div className="col-12 governance-lead-left proposals-header">
            <h2>Proposal details</h2>
          </div>
        </div>
        <div className="spacer-25" />
        <div className="row">
          <div className="col-12">
            {/* proposal box start*/}
            <div className="shadowed-box">
              <div className="row">
                <div className="col-12 proposal-box-top">
                  <ul>
                    <li className="col">
                      ID:{proposal.id} <span className="border-span" /> TEXT
                    </li>
                    <li className="align-self-end">
                      YES {proposal.votesInFavour}{" "}
                      <span className="border-span" />
                    </li>
                    <li className="align-self-end">
                      NO {proposal.votesAgainst}
                    </li>
                  </ul>
                </div>
              </div>
              <div className="spacer-20" />
              <div className="row">
                <div className="col-lg-8">
                  <h4 className="red">
                    <svg
                      id="c-remove"
                      xmlns="http://www.w3.org/2000/svg"
                      width={16}
                      height={16}
                      viewBox="0 0 16 16"
                    >
                      <g id="Group_237" data-name="Group 237">
                        <path
                          id="Path_258"
                          data-name="Path 258"
                          d="M8,0a8,8,0,1,0,8,8A8.024,8.024,0,0,0,8,0Zm3.5,10.1-1.4,1.4L8,9.4,5.9,11.5,4.5,10.1,6.6,8,4.5,5.9,5.9,4.5,8,6.6l2.1-2.1,1.4,1.4L9.4,8Z"
                          fill="#db2525"
                        />
                      </g>
                    </svg>{" "}
                    REJECTED
                  </h4>
                  <div className="spacer-5" />
                  <h3>
                    [{proposal.type}] {proposal.assetName} ({proposal.ticker})
                  </h3>
                </div>
                <div className="col-lg-4 text-lg-end text-center">
                  <div className="spacer-25" />
                  {executable && (
                    <button
                      className="menu-btn"
                      onClick={() => onExecute()}
                    >
                      Execute
                    </button>
                  )}

                  <button
                    className="menu-btn"
                    onClick={() => setShowVoteModal(true)}
                  >
                    Vote
                  </button>
                </div>
              </div>
              <div className="spacer-20" />
              <div className="row proposal-box-footer">
                <div className="col-12 box-end-text">
                  <strong>Creator </strong>
                  {proposal.creator}{" "}
                  {proposal.endedAt ? (
                    <>
                      <span className="border-span" />
                      <strong>Ended </strong>
                      {proposal.endedAt}
                    </>
                  ) : (
                    <></>
                  )}
                </div>
              </div>
            </div>
            {/* proposal box end*/}
            <div className="spacer-25" />
            {/* Deetails - proposals start */}
            <div className="row details-props">
              {/* Details start */}
              <div className="col-xl-8">
                <div className="shadowed-box full-width details-area">
                  <h4>Details</h4>
                  <ol id="more-detail">
                    {proposal.messages?.map((m, i) => (
                      <li key={m + i}>
                        {i + 1}. {m}
                      </li>
                    ))}
                  </ol>
                  <span className="more-details-link" id="moredetail">
                    More Details{" "}
                    <svg
                      id="triangle-down"
                      xmlns="http://www.w3.org/2000/svg"
                      width="9.333"
                      height={7}
                      viewBox="0 0 9.333 7"
                    >
                      <g id="Group_240" data-name="Group 240">
                        <path
                          id="Path_260"
                          data-name="Path 260"
                          d="M8.944,0H.389a.389.389,0,0,0-.32.609L4.346,6.831a.389.389,0,0,0,.641,0L9.265.609A.389.389,0,0,0,8.944,0Z"
                          fill="#21bba6"
                        />
                      </g>
                    </svg>
                  </span>
                  <div className="spacer-30" />
                  <h4>Description</h4>
                  <p>{proposal.description}</p>
                  <div className="spacer-25" />
                </div>
              </div>
              {/* Details end */}
              <div className="spacer-25 d-block d-lg-none" />
              {/* Proposals start*/}
              <div className="col-xl-4 proposal-history">
                <div className="shadowed-box full-width">
                  <h4>Proposal history</h4>
                  <ul className="proposal-list">
                    {proposal.events.map((event) => {
                      return (
                        <li className="passed row">
                          <div className="col-10">
                            <h6>{event.name}</h6>
                            July 2nd, 2021 - 20:23pm
                          </div>
                          <div className="col-2" />
                        </li>
                      );
                    })}
                  </ul>
                </div>
              </div>
              {/* Proposals end*/}
            </div>
            {/* Deetails - proposals end */}
            {/* */}
            <div className="spacer-25" />
            {/* Vote Details start */}
            <VoteDetails proposal={proposal} />
            {/* Vote Details end */}
          </div>
        </div>
        {/*Proposal details End */}
      </div>
      <div className="spacer-20" />
      <div className="spacer-30" />
    </div>
  );
}