import { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { Table, Modal, Nav } from 'react-bootstrap';
import { getBorrowLimitPercentage, getBorrowLimit } from '../wallet/walletSlice';
import { selectAssetById } from '../wallet/walletSlice';
import { selectMarketById } from '../market/marketSlice';
import { mint, redeem } from '../wallet/walletSlice';
import { unwrapResult } from '@reduxjs/toolkit'
import { ConfirmingModal } from '../common/ConfirmingModal'
import { AmountTab } from '../common/AmountTab'
import AmountModal from '../common/AmountModal'
import { assetImages } from '../../images/assets'

const SupplyModal = ({marketId,onClose}) => {
  const dispatch = useDispatch()
  const market = useSelector(state => selectMarketById(state, marketId))
  const walletAsset = useSelector(state => selectAssetById(state, marketId))
  const [confirming, setconfirming] = useState(false)

  const handleClose = () => {
    setconfirming(false)
    onClose()
  }

  const handleWithdrawConfirm = async (amount) => {
    setconfirming(true);
    unwrapResult(await dispatch(
      redeem({ asset: walletAsset, amount })
    ))
    handleClose()
  }
  
  const handleSupplyConfirm = async (amount) => {
    setconfirming(true);
    unwrapResult(await dispatch(
      mint({ asset: walletAsset, amount })
    ))
    handleClose()
  }

  const data = {
    asset: {
      title: market.title,
      image: assetImages[market.ID]
    },
    rateTitle: "Supply",
    rates: {
      asset: market.supplyApy,
      qToken: market.lqApy
    },
    borrowLimitDollars: getBorrowLimit(walletAsset) * market.usdExchange,
    borrowPercentage: getBorrowLimitPercentage(walletAsset),
    addTab: {
      title: "Supply",
      infoTitle: "Wallet Balance",
      infoValue: `${walletAsset.balance} ${market.asset}`,
      available: walletAsset.balance > 0,
      unavailableMessage: "No Funds Available",
      onSubmit: handleSupplyConfirm,
      maxValue: walletAsset.balance,
      validator: (amount) => amount > 0 && amount <= walletAsset.balance
    },
    subtractTab: {
      title: "Withdraw",
      infoTitle: "Currently Supplying",
      infoValue: `${walletAsset.currentSupply} ${market.asset}`,
      available: walletAsset.currentSupply > 0,
      unavailableMessage: "No Balance to Withdraw Available",
      onSubmit: handleWithdrawConfirm,
      maxValue: walletAsset.currentSupply,
      validator: (amount) => amount > 0 && amount <= walletAsset.currentSupply
    }
  }

  if(confirming) {
    return <ConfirmingModal data={data} />
  } else {
    return <AmountModal onClose={handleClose} data={data} />
  }
}

export default SupplyModal;