import { useDispatch } from 'react-redux'
import { useSelector } from 'react-redux'
import Modal from '../common/Modal'
import { selectAssetById, stake, claim } from '../wallet/walletSlice'
import { unwrapResult } from '@reduxjs/toolkit'
import {  selectPoolSize } from './governanceSlice'
import { useHistory } from 'react-router-dom'
import { useState } from 'react'

export default function StakeModal(props) {
  const history = useHistory()
  const dispatch = useDispatch()
  const lqWalletAsset = useSelector(s => selectAssetById(s, "LQ"))
  const currentStake = lqWalletAsset.currentStake || 0
  const poolSize = useSelector(selectPoolSize)
  
  const onStake = async (amount) => {
    unwrapResult(await dispatch(
      stake({ amount: parseInt(amount), asset: lqWalletAsset})
    ))
    props.onClose()
    history.push("/vote")
  }

  const onClaim = async (amount) => {
    unwrapResult(await dispatch(
      claim({ amount: parseInt(amount), asset: lqWalletAsset })
    ))
    props.onClose()
    history.push("/vote")
  }

  const [tab, setTab] = useState("stake")
  const [stakeAmount, setStakeAmount] = useState("")
  const [claimAmount, setClaimAmount] = useState("")
  const proposedStake = parseInt(stakeAmount) || 0
  const proposedClaim = parseInt(claimAmount) || 0
  const stakeAmountValid = proposedStake > 0 && proposedStake <= (lqWalletAsset.balance - currentStake)
  const claimAmountValid = proposedClaim > 0 && proposedClaim <= currentStake
  const poolShare = (currentStake+proposedStake-proposedClaim) === 0 ? 0 : ((currentStake+proposedStake-proposedClaim) / poolSize)*100

  return (
    <Modal dialogClass="pool-connect">
      <div className="modal-content pool-model-content">
        <div className="modal-header text-center">
          <h3 className="modal-title">
          {logo} LQ</h3>
          <button type="button" className="btn-close" aria-label="Close" onClick={props.onClose} />
        </div>
        <div className="modal-body">
          <ul className="nav pool-nav" id="poolTab" role="tablist">
            <li className="nav-item" role="presentation">
              <button className={tab === "stake" ? "active" : ""} onClick={() => setTab("stake")} role="tab" aria-controls="stake" aria-selected="true">
                <span>Stake</span>
              </button>
            </li>
            <li className="nav-item" role="presentation">
              <button className={tab === "claim" ? "active" : ""} onClick={() => setTab("claim")} role="tab" aria-controls="claim" aria-selected="false">
                <span>Claim</span>
              </button>
            </li>
          </ul>
          <div className="tab-content" id="poolTabContent">
            {tab === "stake" && (<div className="tab-pane fade show active" id="stake" role="tabpanel" aria-labelledby="stake-tab">
              <div className="tab-inner">
                <form>
                  <div className="form-fileds">
                    <div className="row">
                      <div className="col-6">Amount</div>
                      <div className="col-6 text-end">Balance {lqWalletAsset.balance.toFixed(6)}</div>
                    </div>
                    <div className="row">
                      <div className="col-6"><input type="text" placeholder="0,000000" value={stakeAmount} onChange={e => setStakeAmount(e.target.value)} /></div>
                      <div className="col-6 text-end"><strong className="white-on-dark">LQ</strong></div>
                    </div>
                  </div>
                </form>
                <div className="pool-pop-limiter">
                  <div className="row">
                    <div className="col-12">
                      <p className="smaller lighter"><i>Protocol fee paid from withdrawn collateral is distributed to LQ stakers and increase stake LQ</i></p>
                    </div>
                  </div>
                  <div className="spacer-15" />
                  <div className="row">
                    <div className="col-6">
                      Pool Share After Tx<span className="info-hover" data-bs-toggle="tooltip" data-bs-placement="top" title="Info text hover"><i className="info-text darker" /></span>
                    </div>
                    <div className="col-6 text-end">{poolShare.toFixed(1)}%</div>
                  </div>
                </div>
                <div className="spacer-15" />
                <div className="pool-pop-up-border" />
                <div className="spacer-15" />
                <div className="pool-pop-limiter">
                  <div className="row">
                    <div className="col-6">
                      Tx Fee<span className="info-hover" data-bs-toggle="tooltip" data-bs-placement="top" title="Info text hover"><i className="info-text darker" /></span>
                    </div>
                    <div className="col-6 text-end">0.05 qADA</div>
                  </div>
                  <div className="spacer-10" />
                  <div className="spacer-30" />
                  <div className="row">
                    <div className="col-12 text-center">
                      <button className={"menu-btn" + (stakeAmountValid ? " menu-btn-enabled" : "")} disabled={!stakeAmountValid} onClick={() => onStake(stakeAmount)}>Stake</button>
                    </div>
                  </div>
                  <div className="spacer-30" />
                </div>
              </div>
            </div>)}
            {tab === "claim" && (<div className="tab-pane fade show active" id="claim" role="tabpanel" aria-labelledby="claim-tab">
              <div className="tab-inner">
                <form>
                  <div className="form-fileds">
                    <div className="row">
                      <div className="col-6">Amount</div>
                      <div className="col-6 text-end">Currently Staking {currentStake}</div>
                    </div>
                    <div className="row">
                      <div className="col-6"><input type="text" placeholder="0,000000" value={claimAmount} onChange={e => setClaimAmount(e.target.value)} /></div>
                      <div className="col-6 text-end"><strong className="white-on-dark">LQ</strong></div>
                    </div>
                  </div>
                </form>
                <div className="pool-pop-limiter">
                  <div className="row">
                    <div className="col-12">
                      <p className="smaller lighter"><i>Protocol fee paid from withdrawn collateral is distributed to LQ stakers and increase stake LQ</i></p>
                    </div>
                  </div>
                  <div className="spacer-15" />
                  <div className="row">
                    <div className="col-6">
                      Pool Share After Tx<span className="info-hover" data-bs-toggle="tooltip" data-bs-placement="top" title="Info text hover"><i className="info-text darker" /></span>
                    </div>
                    <div className="col-6 text-end">{poolShare.toFixed(1)}%</div>
                  </div>
                </div>
                <div className="spacer-15" />
                <div className="pool-pop-up-border" />
                <div className="spacer-15" />
                <div className="pool-pop-limiter">
                  <div className="row">
                    <div className="col-6">
                      Tx Fee<span className="info-hover" data-bs-toggle="tooltip" data-bs-placement="top" title="Info text hover"><i className="info-text darker" /></span>
                    </div>
                    <div className="col-6 text-end">0.05 qADA</div>
                  </div>
                  <div className="spacer-10" />
                  <div className="spacer-30" />
                  <div className="row">
                    <div className="col-12 text-center">
                      <button disabled={!claimAmountValid} className={"menu-btn" + (claimAmountValid ? " menu-btn-enabled" : "")} onClick={() => onClaim(claimAmount)} >Claim</button>
                    </div>
                  </div>
                  <div className="spacer-30" />
                </div>
              </div>
            </div>)}
          </div>
        </div>
      </div>
    </Modal>
  )
}

const logo = (
  <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width={23} height={22} viewBox="0 0 23 22"><defs><pattern id="pattern" preserveAspectRatio="xMidYMid slice" width="100%" height="100%" viewBox="0 0 1067 1067"><image width={1067} height={1067} xlinkHref="data:image/png;base64," /></pattern></defs><rect id="Ergo" width={23} height={22} fill="url(#pattern)" /></svg>
)