import { useState } from "react"
import { useDispatch } from "react-redux"
import { NavLink } from "react-router-dom"
import { toggleTheme } from "../features/theme/themeSlice"
import WalletModal from "../features/wallet/WalletModal.js"
import { ReactComponent as MoonIcon } from "../images/moon.svg"
import { ReactComponent as SunIcon } from "../images/sun.svg"
import { ReactComponent as Logo } from "../images/logo.svg"
import Modal from "../features/common/Modal"
import { useEffect } from "react"

const WalletSelectorModal = (props) => {
  const connectNami = () => {
    window.cardano.enable().then(r => {
      props.onConnect()
      props.onClose()
    })
  }

  return (
    <Modal>
      <div className="modal-content vote-model-content">
        <div className="modal-header text-center">
          <h3 className="modal-title">Choose Wallet</h3>
          <button
            type="button"
            className="btn-close"
            onClick={props.onClose}
            aria-label="Close"
          />
        </div>
        <div className="modal-body">
          <div>
            <ul>
              <li style={{ cursor: "pointer" }}
                onClick={() => connectNami()}>NAMI</li>
            </ul>
          </div>
        </div>
      </div>
    </Modal>
  );
}

const WalletConnector = () => {
  const [showWalletSelectorModal, setShowWalletSelectorModal] = useState(false);
  const [isConnected, setIsConnected] = useState(false)
  const [walletAddress, setWalletAddress] = useState(null);
  const setConnectedStatus = () => {
    if(window.cardano) {
      window.cardano.isEnabled().then((r) => {
        if(r) {
          setIsConnected(r)
          window.cardano.getUsedAddresses().then(addresses => {
            setWalletAddress(addresses[0])
          })
        }
      });
    }
  }
  useEffect(() => setConnectedStatus())
  const handleConnectClick = (e) => {
    setShowWalletSelectorModal(true)
    e.preventDefault()
  }
  const onConnect = (b) => {
    debugger
    setConnectedStatus()
  }

  return (
    <>
      {isConnected ? (
        <button disabled className="btn menu-btn">
          Wallet: {walletAddress?.substr(0,5)}
        </button>
      ) : (
        <a onClick={handleConnectClick} href="/" className="btn menu-btn">
          Connect wallet
        </a>
      )}

      {showWalletSelectorModal && (
        <WalletSelectorModal
          onClose={() => setShowWalletSelectorModal(false)}
          onConnect={onConnect}
        />
      )}
    </>
  );
}

const LogoMenu = () => {
  const dispatch = useDispatch()
  const [showWalletModal, setShowWalletModal] = useState(false)
  

  const handleShowWalletModal = () => setShowWalletModal(true);
  const handleCloseWalletModal = () => setShowWalletModal(false);
  return (
    <>
      {showWalletModal && (
        <WalletModal show={showWalletModal} onHide={handleCloseWalletModal} />
      )}

      <div className="row logo-menu-row">
        <div className="col-5 col-md-5 col-lg-3">
          <div className="spacer-15 d-none d-sm-block" />
          <a href="#" className="site-logo">
            <Logo />
          </a>
        </div>
        <div
          id="main-menu"
          className="col-sm-6 text-center header-menu-area center-menu"
        >
          <div className="spacer-20" />
          <div className="spacer-5" />
          <ul className="text-sm-start site-menu">
            <li>
              <NavLink
                to="/"
                activeClassName="active"
                className="close-menu"
                exact
              >
                Dashboard
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/vote"
                activeClassName="active"
                className="close-menu"
                exact
              >
                Vote
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/staking"
                activeClassName="active"
                className="close-menu"
                exact
              >
                Staking
              </NavLink>
            </li>
            <li className="d-block d-sm-none mobile-dark-light">
              <a
                href="#"
                onClick={() => dispatch(toggleTheme({}))}
                className="light-dark-switch-mobile"
              >
                <MoonIcon />
                <SunIcon />
              </a>
            </li>
          </ul>
        </div>
        <div className="col-7 col-md-7 col-lg-3 header-menu-area header-menu-right text-sm-end">
          <ul className="text-sm-start site-menu">
            <li>
              <WalletConnector />
            </li>
          </ul>
          <div id="burger-menu">
            <span />
            <span />
            <span />
          </div>
          <a
            href="#"
            onClick={() => dispatch(toggleTheme({}))}
            className="light-dark-switch"
          >
            <MoonIcon />
            <SunIcon />
          </a>
        </div>
      </div>
    </>
  );
}

export default LogoMenu