import Page from "./Page"
import { useHistory } from "react-router-dom";
import { useState } from "react";
import { unwrapResult } from '@reduxjs/toolkit'
import { useDispatch, useSelector } from "react-redux";
import { createProposal } from "../features/governance/governanceSlice";
import { useForm } from "react-hook-form";
import Fraction from "fraction.js"
import { selectAssetById } from "../features/wallet/walletSlice";

const percentageToFraction = (str) => {
  return new Fraction(parseFloat(str) / 100);
}

export default function Whitelist() {
  const history = useHistory()
  const { register, handleSubmit, formState: { errors } } = useForm();
  const lqWalletAsset = useSelector((s) => selectAssetById(s, "LQ"));
  const fee = 0.17
  const insufficientBalance = lqWalletAsset.balance < fee
  
  const [loading,setLoading] = useState(false)
  const dispatch = useDispatch()
  const onSubmit = async data => {

    let collateralFactorFrac = percentageToFraction(data.collateralFactor)
    let reserveFactorFrac = percentageToFraction(data.reserveFactor)
    let apyBorrowBaseRateFrac = percentageToFraction(data.apyBorrowBaseRate)
    let apyBorrowRateKinkFrac = percentageToFraction(data.apyBorrowRateKink)
    let kinkUtilizationFrac = percentageToFraction(data.kinkUtilization)
    let apyBorrowRateAt100Frac = percentageToFraction(data.apyBorrowRateAt100)

    const proposal = {
      interestModel: {
        baseRate: {
          numerator: apyBorrowBaseRateFrac.n,
          denominator: apyBorrowBaseRateFrac.d,
        },
        kink: {
          "borrowed supply": apyBorrowRateKinkFrac.n,
          "total supply": apyBorrowRateKinkFrac.d,
        },
        utilMultiplier: {
          numerator: kinkUtilizationFrac.n,
          denominator: kinkUtilizationFrac.d,
        },
        utilMultiplierJump: {
          numerator: apyBorrowRateAt100Frac.n,
          denominator: apyBorrowRateAt100Frac.d,
        },
      },
      collateralRatio: {
        Borrowable: collateralFactorFrac.n,
        Collateral: collateralFactorFrac.d,
      },
      reserveRatio: {
        "Paid to Reserves": reserveFactorFrac.n,
        "Paid to Market": reserveFactorFrac.d,
      },
      ...data,
    };
    setLoading(true)
    unwrapResult(await dispatch(
      createProposal(proposal)
    ))
    setLoading(false)
    history.push("/vote")
  }

  return (
    <Page>
      <div className="spacer-20" />
      <div className="spacer-30" />
      <div className="container wider choose-proposal">
        <div className="row">
          <div className="col-12 governance-lead-left pools-header">
            <h2>Whitelist a new asset</h2>
          </div>
        </div>
        <div className="spacer-25" />
        <div className="row wider">
          <div className="col-12">
            <div className="shadowed-box full-width">
              <div className="row">
                <div className="col-12">
                  <div className="whitelist-form-container">
                    <div className="spacer-25" />
                    <p>Token Name</p>
                    {errors.tokenName && (
                      <span className="text-danger">
                        This field is required
                      </span>
                    )}
                    <input
                      type="text"
                      {...register("tokenName", { required: true })}
                    />

                    <p>Ticker</p>
                    {errors.ticker && (
                      <span className="text-danger">
                        This field is required
                      </span>
                    )}
                    <input
                      type="text"
                      {...register("ticker", { required: true })}
                    />

                    <p>Collateral Factor</p>
                    {errors.collateralFactor && (
                      <span className="text-danger">
                        This field is required
                      </span>
                    )}
                    <input
                      type="text"
                      {...register("collateralFactor", { required: true })}
                    />

                    <p>Reserve Factor</p>
                    {errors.reserveFactor && (
                      <span className="text-danger">
                        This field is required
                      </span>
                    )}
                    <input
                      type="text"
                      {...register("reserveFactor", { required: true })}
                    />

                    <hr></hr>
                    <h4>Interest Rate Model</h4>

                    <p>APY Borrow Base Rate</p>
                    {errors.apyBorrowBaseRate && (
                      <span className="text-danger">
                        This field is required
                      </span>
                    )}
                    <input
                      type="text"
                      {...register("apyBorrowBaseRate", { required: true })}
                    />

                    <p>APY Borrow Rate at Kink</p>
                    {errors.apyBorrowRateKink && (
                      <span className="text-danger">
                        This field is required
                      </span>
                    )}
                    <input
                      type="text"
                      {...register("apyBorrowRateKink", { required: true })}
                    />

                    <p>Kink Utilization</p>
                    {errors.kinkUtilization && (
                      <span className="text-danger">
                        This field is required
                      </span>
                    )}
                    <input
                      defaultValue="80"
                      {...register("kinkUtilization", { required: true })}
                    />

                    <p>APY Borrow Rate at 100%</p>
                    {errors.apyBorrowRateAt100 && (
                      <span className="text-danger">
                        This field is required
                      </span>
                    )}
                    <input
                      type="text"
                      {...register("apyBorrowRateAt100", { required: true })}
                    />

                    <hr></hr>

                    <p>
                      Suggested Oracle
                      <span
                        className="info-hover"
                        data-bs-toggle="tooltip"
                        data-bs-placement="top"
                        title="Info text hover"
                      >
                        <i className="info-text darker" />
                      </span>
                    </p>
                    {errors.oracle && (
                      <span className="text-danger">
                        This field is required
                      </span>
                    )}
                    <input
                      type="text"
                      {...register("oracle", { required: false })}
                    />

                    <p>
                      Description
                      <span
                        className="info-hover"
                        data-bs-toggle="tooltip"
                        data-bs-placement="top"
                        title="Info text hover"
                      >
                        <i className="info-text darker" />
                      </span>
                    </p>
                    {errors.oracle && (
                      <span className="text-danger">
                        This field is required
                      </span>
                    )}
                    <textarea
                      {...register("description", { required: false })}
                    />

                    <div className="row">
                      <div className="col text-end">
                        Balance: {lqWalletAsset.balance}
                      </div>
                    </div>
                    <div className="form-infos darker">
                      <div className="row">
                        <div className="col-6 info-header">
                          Tx Fee
                          <span
                            className="info-hover"
                            data-bs-toggle="tooltip"
                            data-bs-placement="top"
                            title="Info text hover"
                          >
                            <i className="info-text darker" />
                          </span>
                        </div>
                        <div className="col-6 text-end">{fee} ADA</div>
                      </div>
                    </div>
                    {insufficientBalance && (
                      <p className="red">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16.003"
                          height="15.001"
                          viewBox="0 0 16.003 15.001"
                        >
                          <g
                            id="warning-sign"
                            transform="translate(0.001 0.001)"
                          >
                            <g id="Group_271" data-name="Group 271">
                              <path
                                id="Path_271"
                                data-name="Path 271"
                                d="M15.8,12.526,9.483.88A1.668,1.668,0,0,0,8.8.2,1.693,1.693,0,0,0,6.516.88L.2,12.526A1.678,1.678,0,0,0,1.686,15H14.314a1.7,1.7,0,0,0,.8-.2,1.673,1.673,0,0,0,.687-2.274ZM8,13a1,1,0,1,1,1-1A1,1,0,0,1,8,13ZM9,9.5a.5.5,0,0,1-.5.5h-1A.5.5,0,0,1,7,9.5v-4A.5.5,0,0,1,7.5,5h1a.5.5,0,0,1,.5.5Z"
                                fill="#db2525"
                              />
                            </g>
                          </g>
                        </svg>{" "}
                        Insufficient balance
                      </p>
                    )}
                    <div className="spacer-15 d-block d-sm-none" />
                    <div className="row">
                      <div className="col-12 text-center">
                        <button
                          className="menu-btn"
                          onClick={handleSubmit(onSubmit)}
                        >
                          {loading ? "Submitting..." : "Submit"}
                        </button>
                      </div>
                    </div>
                    <div className="spacer-25" />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
}