
export const calculateVoteRatios = (proposal, poolSize) => {
  const votesYes = proposal.votes
    .filter((v) => v.position)
    .map((v) => v.balance)
    .reduce((a, v) => a + v, 0);
  const votesNo = proposal.votes
    .filter((v) => !v.position)
    .map((v) => v.balance)
    .reduce((a, v) => a + v, 0);
  const votesTotal = votesYes + votesNo;
  const votesToPoolRatio = votesTotal === 0 ? 0 : votesTotal / poolSize;
  const yesRatio = votesTotal === 0 ? 0 : votesYes / votesTotal;
  const noRatio = votesTotal === 0 ? 0 : votesNo / votesTotal;
  return {
    votesYes,
    votesNo,
    votesTotal,
    yesRatio,
    noRatio,
    votesRatio: votesToPoolRatio
  };
};