  //               </tr>
  //               <tr>
  //                 <td>Current Supply</td>
  //                 <td aria-label="current-supply">{walletAsset.currentSupply.toFixed(4)}</td>
  //               </tr>
  //               <tr>
  //                 <td>Current Borrow</td>
  //                 <td aria-label="current-borrow">{walletAsset.currentBorrow.toFixed(4)}</td>
  //               </tr>
  //               <tr>
