import { useDispatch } from 'react-redux'
import { useSelector } from 'react-redux'
import Modal from '../common/Modal'
import { selectAssetById } from '../wallet/walletSlice'
import { unwrapResult } from '@reduxjs/toolkit'
import { selectProposalById, vote } from './governanceSlice'
import { useHistory } from 'react-router-dom'
import { useState } from 'react'

export default function VoteModal(props) {
  //TODO: Remove amount input
  const [position, setPosition ] = useState(true)
  const lqWalletAsset = useSelector(s => selectAssetById(s,"LQ"))
  const proposal = useSelector(state => selectProposalById(state, parseInt(props.proposalId)))
  const history = useHistory()
  const dispatch = useDispatch()
  const positionToggle = () => setPosition(!position)
  const [loading, setLoading] = useState(false)
  const onSubmit = async () => {
    setLoading(true)
    unwrapResult(await dispatch(
      vote({proposal, position})
    ))
    setLoading(false)
    props.onClose()
  }

  return (
    <Modal id="voteModal" dialogClass="vote-connect">
      <div className="modal-content vote-model-content">
        <div className="modal-header text-center">
          <h3 className="modal-title">Vote</h3>
          <button type="button" className="btn-close" onClick={props.onClose} aria-label="Close" />
        </div>
        <div className="modal-body">
          <ul className="nav vote-nav" id="voteTab" role="tablist">
            <li className="nav-item" role="presentation">
              <button id="stake-tab" className={position ? "active" : ""} onClick={positionToggle} data-bs-toggle="tab" data-bs-target="#stake" role="tab" aria-controls="stake" aria-selected="true">
                <span>Yes</span>
              </button>
            </li>
            <li className="nav-item" role="presentation">
              <button id="claim-tab" className={position ? "" : "active"} onClick={positionToggle} data-bs-toggle="tab" data-bs-target="#claim" role="tab" aria-controls="claim" aria-selected="false">
                <span>No</span>
              </button>
            </li>
          </ul>
          <div className="tab-content" id="voteTabContent">
            <div className="tab-pane fade show active" id="stake" role="tabpanel" aria-labelledby="stake-tab">
              <div className="tab-inner">
                <div className="spacer-25"></div>
                <div className="pool-pop-limiter">
                  <div className="row back-grounded-area">
                    <div className="col-6">
                      Tx Fee<span className="info-hover" data-bs-toggle="tooltip" data-bs-placement="top" title="Info text hover"><i className="info-text" /></span>
                    </div>
                    <div className="col-6 text-end">0.1 UST</div>
                  </div>
                  <div className="spacer-10" />
                  <div className="row">
                    <div className="col-12">
                      <div className="important-text">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16.172" height="15.001" viewBox="0 0 16.172 15.001">
                          <g id="Group_271" data-name="Group 271" transform="translate(0)">
                            <path id="Path_271" data-name="Path 271" d="M15.966,12.526,9.583.88A1.677,1.677,0,0,0,8.893.2,1.72,1.72,0,0,0,6.585.88L.2,12.526a1.663,1.663,0,0,0,.039,1.66A1.7,1.7,0,0,0,1.7,15H14.465a1.732,1.732,0,0,0,.808-.2,1.677,1.677,0,0,0,.826-.994,1.657,1.657,0,0,0-.132-1.28ZM8.084,13a1,1,0,1,1,1.011-1A1.005,1.005,0,0,1,8.084,13ZM9.095,9.5a.5.5,0,0,1-.505.5H7.579a.5.5,0,0,1-.505-.5v-4A.5.5,0,0,1,7.579,5H8.589a.5.5,0,0,1,.505.5Z" transform="translate(0.001 0.001)" fill="#db2525" />
                          </g>
                        </svg>
                        <p>Vote cannot be changed after submission. Staked LQ used to vote in proposals are locked and cannot be withdrawn until proposal finishes.</p>
                      </div>
                    </div>
                  </div>
                  <div className="spacer-25" />
                  <div className="row">
                    <div className="col-12 text-center">
                      <button onClick={onSubmit} className="menu-btn">Submit</button>
                    </div>
                  </div>
                  <div className="spacer-35" />
                </div>
              </div>
            </div>
            <div className="tab-pane fade" id="claim" role="tabpanel" aria-labelledby="claim-tab">
              <div className="tab-inner">
                <div className="pool-pop-limiter">
                  <div className="row back-grounded-area">
                    <div className="col-6">
                      Tx Fee <span className="info-hover" data-bs-toggle="tooltip" data-bs-placement="top" title="Info text hover"><i className="info-text" /></span>
                    </div>
                    <div className="col-6 text-end">0.1 UST</div>
                  </div>
                  <div className="spacer-10" />
                  <div className="row">
                    <div className="col-12">
                      <div className="important-text">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16.172" height="15.001" viewBox="0 0 16.172 15.001">
                          <g id="Group_271" data-name="Group 271" transform="translate(0)">
                            <path id="Path_271" data-name="Path 271" d="M15.966,12.526,9.583.88A1.677,1.677,0,0,0,8.893.2,1.72,1.72,0,0,0,6.585.88L.2,12.526a1.663,1.663,0,0,0,.039,1.66A1.7,1.7,0,0,0,1.7,15H14.465a1.732,1.732,0,0,0,.808-.2,1.677,1.677,0,0,0,.826-.994,1.657,1.657,0,0,0-.132-1.28ZM8.084,13a1,1,0,1,1,1.011-1A1.005,1.005,0,0,1,8.084,13ZM9.095,9.5a.5.5,0,0,1-.505.5H7.579a.5.5,0,0,1-.505-.5v-4A.5.5,0,0,1,7.579,5H8.589a.5.5,0,0,1,.505.5Z" transform="translate(0.001 0.001)" fill="#db2525" />
                          </g>
                        </svg>
                        <p>Vote cannot be changed after submission. Staked LQ used to vote in proposals are locked and cannot be withdrawn until proposal finishes.</p>
                      </div>
                    </div>
                  </div>
                  <div className="spacer-25" />
                  <div className="row">
                    <div className="col-12 text-center">
                      <button onClick={onSubmit} className="menu-btn">{loading ? "Submitting..." : "Submit"}</button>
                    </div>
                  </div>
                  <div className="spacer-35" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Modal>
  )
}