import { useState } from 'react';
import { useSelector } from 'react-redux';
import { selectAssetById } from '../wallet/walletSlice';
import { selectMarketById } from '../market/marketSlice';
import SupplyModal from './SupplyModal'
import imgG16 from "../../images/only-app/Group16.png"
import { assetImages } from '../../images/assets';

const SupplyMarket = ({ marketId }) => {
  const market = useSelector(state => selectMarketById(state,marketId))
  const walletAsset = useSelector(state => selectAssetById(state,marketId))

  const [showModal, setShowModal] = useState(false)
  
  const balance = walletAsset ? walletAsset.balance : 0

  return (
    <div className="row contents">
      <div className="col-md-4 col-4" data-testid={"supply-button-" + market.asset} onClick={_ => setShowModal(true)}>
        <img src={assetImages[market.ID]} className="coin-icon" width="22px" /> {market.asset}
        <span className="apply-only-mobile d-block d-sm-none">{market.supplyApy.toFixed(2)}%</span>
      </div>
      <div className="col-md-8 col-8 align-self-center">
        <div className="row">
          <div className="col-md-4 d-none d-sm-block text-end">{market.supplyApy.toFixed(2)}%</div>
          <div data-testid={"wallet-balance-" + market.asset} className="col-md-8 col-12 text-end">{balance} {market.asset}</div>

        </div>
      </div>
      {showModal &&
        <SupplyModal marketId={marketId} onClose={() => setShowModal(false)} />
      }
    </div>
  )
}

export default SupplyMarket