import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import superagent from 'superagent'
import { execute } from "../governance/governanceSlice";
import { selectUsdExchangeForId} from '../market/marketSlice'

// -- async

export const mint = createAsyncThunk('wallet/mint', async payload => {
  await superagent.put("http://localhost:8090/wallet/mint")
    .send(payload)

  return payload
})

export const redeem = createAsyncThunk('wallet/redeem', async payload => {
  await superagent.put("http://localhost:8090/wallet/redeem")
    .send(payload)

  return payload
})

export const borrow = createAsyncThunk('wallet/borrow', async payload => {
  await superagent.put("http://localhost:8090/wallet/borrow")
    .send(payload)

  return payload
})

export const repay = createAsyncThunk('wallet/repay', async (payload, context) => {
  const asset = getAssetByName(context.getState().wallet, payload.asset.name)
  if (payload.amount === asset.currentBorrow) {
    await superagent.put("http://localhost:8090/wallet/repayAll")
      .send(payload)
  } else {
    await superagent.put("http://localhost:8090/wallet/repay")
      .send(payload)
  }

  return payload
})

export const stake = createAsyncThunk('wallet/stake', async payload => {
  await superagent.put("http://localhost:8090/wallet/stake")
    .send(payload)

  return payload
})

export const claim = createAsyncThunk('wallet/claim', async payload => {
  await superagent.put("http://localhost:8090/wallet/claim")
    .send(payload)

  return payload
})

// -- selectors

const selectAssets = state => state.wallet.assets
export const selectAssetById = (state, id) => state.wallet.assets.find(a => a.ID === id)

export const selectTotalSupplyInUsd = state => {
  return selectAssets(state)
    .map(asset => asset.currentSupply * selectUsdExchangeForId(state,asset.ID))
    .reduce((acc,val) => acc + val, 0)
}

export const selectTotalBorrowInUsd = state => {
  return selectAssets(state)
    .map(asset => asset.currentBorrow * selectUsdExchangeForId(state, asset.ID))
    .reduce((acc, val) => acc + val, 0)
}

export const selectNetAPY = state => {
  return 0
}

export const selectTotalBorrowLimitRatio = state => {
  return 0.0
}

export const selectTotalBorrowLimitUSD = state => {
  return 0.0
}


// -- helpers

export const getAssetByName = (state, name) => state.assets.find(a => a.name === name)

const borrowLimitForSupply = (supply) => ((supply * 0.2) * 1.5)

export const getBorrowLimit = asset => {
  if(!!!asset.currentSupply) {
    return 0
  } else {
    return borrowLimitForSupply(asset.currentSupply)
  }
}

export const getBorrowLimitPercentage = asset => {
  if (!!!asset.currentSupply) {
    return 0
  } else if (!!!asset.currentBorrow) {
    return 0
  } else {
    return borrowLimitForSupply(asset.currentSupply) / asset.currentBorrow
  }
}

export const walletSlice = createSlice({
  name: "wallet",
  initialState: {
    address: "lqTest",
    assets: [
      {
        ID: "ADA",
        name: "ADA",
        title: "Cardano",
        balance: 25000,
        currentSupply: 0,
        currentBorrow: 0,
        borrowLimitRatio: 0.9,
        fee: 10,
        isOn: false,
      },
      {
        ID: "ERG",
        name: "ERG",
        title: "Ergo",
        balance: 1000,
        currentSupply: 0,
        currentBorrow: 0,
        borrowLimitRatio: 0.9,
        fee: 10,
        isOn: false,
      },
      {
        ID: "LQ",
        name: "LQ",
        title: "Liqwid",
        balance: 100,
        currentSupply: 0,
        currentBorrow: 0,
        borrowLimitRatio: 0.9,
        fee: 10,
        isOn: false,
      },
      {
        ID: "AGIX",
        name: "AGIX",
        title: "Singularity NET",
        balance: 15000,
        currentSupply: 0,
        currentBorrow: 0,
        borrowLimitRatio: 0.9,
        fee: 10,
        isOn: false,
      },
    ],
  },
  reducers: {
    toggleSupply(state, { payload }) {
      let asset = getAssetByName(state, payload.asset);
      asset.isOn = true;
    },
  },
  extraReducers: {
    [mint.fulfilled]: (state, { payload }) => {
      const asset = getAssetByName(state, payload.asset.name);
      const total = payload.amount + asset.fee;
      asset.currentSupply += payload.amount;
      asset.balance -= total;
    },
    [redeem.fulfilled]: (state, { payload }) => {
      const asset = getAssetByName(state, payload.asset.name);
      const total = payload.amount - asset.fee;
      asset.currentSupply -= payload.amount;
      asset.balance += total;
    },
    [borrow.fulfilled]: (state, { payload }) => {
      const asset = getAssetByName(state, payload.asset.name);
      const total = payload.amount - asset.fee;
      asset.currentBorrow += payload.amount;
      asset.balance += total;
    },
    [repay.fulfilled]: (state, { payload }) => {
      const asset = getAssetByName(state, payload.asset.name);
      const total = payload.amount + asset.fee;
      asset.currentBorrow -= payload.amount;
      asset.balance -= total;
    },
    [stake.fulfilled]: (state, { payload }) => {
      const asset = getAssetByName(state, payload.asset.name);
      asset.currentStake = payload.amount;
      asset.balance = asset.balance - payload.amount;
    },
    [claim.fulfilled]: (state, { payload }) => {
      const asset = getAssetByName(state, payload.asset.name);
      asset.currentStake -= payload.amount;
      asset.balance += payload.amount;
    },
    [execute.fulfilled]: (state, { payload }) => {
      const proposal = payload;
      state.assets.push({
        ID: proposal.ticker,
        name: proposal.ticker,
        title: proposal.tokenName,
        balance: 0,
        currentSupply: 0,
        currentBorrow: 0,
        borrowLimitRatio: 0.9,
        fee: 10,
        isOn: false,
      });
    },
  },
});

export const {handleLQStake} = walletSlice.actions