import ReactDOM from 'react-dom';
import { useEffect, useState } from 'react';


const Modal = ({ children, id, dialogClass}) => {
  const [showClass,setShowClass] = useState("")
  useEffect(() => setShowClass(" show"))

  return ReactDOM.createPortal(
    <>
      <div className={"modal fade" + showClass} id={id} tabIndex="-1" style={{ "display": "block", "paddingLeft": "0px" }} aria-modal="true" role="dialog">
        <div className={"modal-dialog modal-dialog-centered " + (dialogClass ? dialogClass : "")}>
          {children}
        </div>
      </div>
      <div className="modal-backdrop fade show"></div>
    </>
    , document.getElementById("modal-content") || document.createElement('div')
  )
}

export default Modal;