import { useSelector } from "react-redux";
import {
  selectPoolSize,
} from "./governanceSlice";
import { calculateVoteRatios } from "./voteUtils";

const ProposalDetails = ({proposal}) =>  {
  const poolSize = useSelector(selectPoolSize);
  const ratios = calculateVoteRatios(proposal, poolSize);
  return (
    <div className="shadowed-box full-width vote-details">
      <div className="row">
        <div className="col-12">
          <h3>Vote Details</h3>
        </div>
      </div>
      <div className="line-full" />
      <div className="spacer-25" />
      <div className="row progress-bar-area">
        <div className="col-12 bar-text">Threshold</div>
        <div className="col-12">
          <div className="progress" style={{ height: "10px" }}>
            <div
              className="progress-bar"
              role="progressbar"
              style={{
                width: `${ratios.votesToPoolRatio * 100}%`,
                backgroundColor: "#DB2525",
                zIndex: 2,
              }}
              aria-valuenow={ratios.votesToPoolRatio * 100}
              aria-valuemin={0}
              aria-valuemax={100}
            />
            <div
              className="progress-bar"
              role="progressbar"
              style={{
                width: `${(ratios.votesYes / poolSize) * 100}%`,
                backgroundColor: "#21bba6",
                marginLeft: "-5px",
                zIndex: 1,
              }}
              aria-valuenow={(ratios.votesYes / poolSize) * 100}
              aria-valuemin={0}
              aria-valuemax={100}
            />
          </div>
        </div>
        <div className="spacer-30" />
        <div className="col-12">
          <div className="row">
            <div className="col-md-2" />
            <div className="col-md-8">
              <div className="row">
                <div className="col-6 text-center yes-text">
                  <div>YES</div>
                  <div>{ratios.yesRatio.toFixed(2) * 100}%</div>
                  <div>
                    <small>{ratios.votesYes} LQ</small>
                  </div>
                </div>
                <div className="col-6 text-center no-text">
                  <div>NO</div>
                  <div>{ratios.noRatio.toFixed(2) * 100}%</div>
                  <div>
                    <small>{ratios.votesNo} LQ</small>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-2" />
          </div>
        </div>
        <div className="spacer-30" />
        <div className="col-12">
          <div className="row vote-detail-table">
            <div className="col-md-6 col-6">
              <strong>Voter</strong>
            </div>
            <div className="col-md-4 col-3 text-center">
              <strong>Vote</strong>
            </div>
            <div className="col-md-2 col-3 text-end">
              <strong>Balance</strong>
            </div>
          </div>
          <div className="line-full" />
          {proposal.votes.map((vote) => {
            return (
              <div className="row vote-detail-table">
                <div className="col-md-6 col-6">{vote.address}</div>
                <div className="col-md-4 col-3 text-center">
                  {vote.position ? "YES" : "NO"}
                </div>
                <div className="col-md-2 col-3 text-end">{vote.balance} LQ</div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default ProposalDetails